.VoteBox {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    padding: 35px;
    width: 900px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.VoteBtn {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 170px;
    height: 170px;
    border:2px solid;
    border-radius: 15px;
    margin-right: 5px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.UnlockScreen, .LockingDiv {
    width: 0;
    height: 0;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
}

.LockScreen {
    width: 100% !important;
    height: 100% !important;
    z-index: 1000 !important;
    display: block !important;
    background-color: black;
    opacity: 0.75;
}
.WhiteText {
    color: white;
}
.rate {
    font-size: 80pt;
    color: red;
}
@media screen and (max-width: 992px) {
    .VoteBox {
      width: 700px;
    }
  }
  
@media screen and (max-width: 800px) {
    .VoteBox {
        width: 600px;
    }
}

@media screen and (max-width: 700px) {
    .VoteBox {
        width: 500px;
    }
}

@media screen and (max-width: 600px) {
    .VoteBox {
        width: 400px;
    }
}
