.NameBox {
  width: 900px;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  padding: 25px;
}

h2, h4 {
    text-align: center;
}

.NameForm{
    margin-top: 50px;
}

.NameForm input {
    text-align: center;
    font-size: xx-large;
}

@media screen and (max-width: 992px) {
    .NameBox {
      width: 700px;
    }
  }
  
@media screen and (max-width: 800px) {
    .NameBox {
        width: 600px;
    }
}

@media screen and (max-width: 700px) {
    .NameBox {
        width: 500px;
    }
}

@media screen and (max-width: 600px) {
    .NameBox {
        width: 400px;
    }
}